* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
  }
/* Tablet Styles */
@media only screen and (min-width: 401px) and (max-width: 960px) {
 
  }
  /* Desktop Styles */
@media only screen and (min-width: 961px) { 
  .dropdownss {
    position: relative;
    display: inline-block;
  }
  
  .dropdown-contentss {
    display: none;
    position: absolute;
    left:-340px;
    background-color:#18191A ;
    font-size: 13px;
    z-index: 1;
    width: 170px;
    padding-top: 30px;
    padding-left: 15px;
    padding-bottom: 20px;
  }
  
  .dropdown-contentss a {
    text-decoration: none;
    display: block;
    font-weight: 700;
    font-size: 1.2em;
    text-align: left;
  }
  .dropdownss:hover .dropdown-contentss {display: block;}
  .ambassHead{
    display: flex;
     justify-content: space-between;
     align-items: center;
      background-color:#18191A;
      height: 80px;
  }
  .ambassBtn{
      width: 180px;
      border: 2px solid white;
      border-radius: 25px;
      height: 35px;
      font-weight: 600;
      margin-right: 30px;
      background-color:#18191A;
      color: white;
  }
  }