* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
  }
  .img-fluid{
    width: 100%;
    height: 50vh;
  }
  .contentback{
    height: 90vh;
    background-color: black;
  }
  .ovrslayes{
    display: none;
  }
  .btundis{
    display: block;
  }
  .joinsign{
    border:2px solid black;
     background-color:black;
     color: white;
}
.joinsignbtn, .joinsign{
  width:140px;
  height:36px; 
  border:none; 
  background-color:black; 
  color:white;
  font-weight:bold
}
/* Tablet Styles */
@media only screen and (min-width: 401px) and (max-width: 960px) {
  .contentback{
    height: 70vh;
    background-color: black;
  }
  .img-fluid{
    height: 90vh;
  }
  .joinsignbtn, .joinsign{
    width:140px;
    height:36px; 
    border:none; 
    background-color:black; 
    color:white;
    font-weight:bold
}
    .joinsignbtn:hover{
    background-color: white;
    color: black;
}
.joinsign{
    border:2px solid black;
     background-color:black;
     color: white;
}
.joinsign:hover{
    background-color: white;
    color: black;
}
.btundis{
  display: none;
}
.homedis {
  display: none;
}
.ovrslayes{
  display: block;
}
  }
