* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
  }
/* Tablet Styles */
@media only screen and (min-width: 401px) and (max-width: 960px) {
 
  }
  /* Desktop Styles */
@media only screen and (min-width: 961px) {
    .dropdown {
        position: relative;
        display: inline-block;
      }
      
      .dropdown-contents {
        display: none;
        width: 140px;
        position: absolute;
        background-color: #fff7d8;
        font-size: 15px;
        padding-left: 20px;
        padding-right: 20px;
        z-index: 1;
      }
      
      .dropdown-contents a {
        text-decoration: none;
        display: block;
        padding-top: 5px;
        padding-bottom: 5px;
      }
      .dropdown:hover .dropdown-contents {display: block;}
      .textHover{
      color: white;
      font-weight: 500;
      cursor: pointer;
      }  
     .textHover:hover{
     color: #ffc629;
     text-decoration:none;
     }    

}