* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
  }
  .headArrange{
    display: flex;
    justify-content: space-between;
  }
  .tempdraw{
    background-color: black;
    color: white; 
    height: 100vh;
    width: 100%;
    position: fixed;
    top: 0;
    left: 0;
    z-index: 110;
    padding: 20px 30px 20px 30px;
  }
  .headOption{
    display: none
  }

/* Tablet Styles */
@media only screen and (min-width: 401px) and (max-width: 960px) {
  .headArrange{
    display: flex;
    justify-content: space-between;
  }
  .tempdraw{
    background-color: black;
    color: white; 
    height: 100vh;
    width: 100%;
    position: fixed;
    top: 0;
    left: 0;
    z-index: 110;
    padding: 20px 30px 20px 30px;
  }
  .headOption{
    display: none
  }
  }
  /* Desktop Styles */
@media only screen and (min-width: 961px) {
   .headArrange{
    height: 60px;
    padding-left: 15px;
       display: flex;
       justify-content: space-between;
       align-items: center;
      
   }
   .headOption{
       width: 38%;
       height: 60px;
       margin-top: 5px;
       display: flex;
       justify-content: space-between;
       align-items: center;
       font-weight: bold;
   }  
  .dropdown {
    position: relative;
    display: inline-block;
  }
  
  .dropdown-content {
    display: none;
    position: absolute;
    background-color: black;
    font-size: 13px;
    z-index: 1;
  }
  
  .dropdown-content a {
    padding: 5px 10px;
    text-decoration: none;
    display: block;
  }
  .dropdown:hover .dropdown-content {display: block;}

  .downloadBtn{
    background-color: black;
     height: 70px;
      display: flex; 
      align-items: center;
      color: white;
      padding-right: 10px; 
      padding-left: 10px;
      cursor: pointer;
  }
  .downloadBtn:hover{
    background-color: #454650;
  }
  .tabdraw{
    display: none;
  }
  }