* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
  }
.btundiss{
    display: block;
}
.joinndiss{
    display: none;
}
.followstyl{
    display: flex;
    flex-direction: column;
}
.fool{
    display: none;
}

/* Tablet Styles */
@media only screen and (min-width: 401px) and (max-width: 960px) {
    .btundiss{
        display: none;
    }
    .joinndiss{
        display: block;
    }
    .followstyl{
        display: flex;
        flex-direction: column;
    }
  }
  /* Desktop Styles */
@media only screen and (min-width: 961px) {
    .btundiss{
        display: none;
    }
    .joinndiss{
        display: block;
    }
    .followstyl{
        display: flex; justify-content: space-between; width: 35%; margin: auto; align-items: center
    }
  }