* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
  }
  .videodate{
   width: 95%;
   height: 50vh;
}


/* Tablet Styles */
@media only screen and (min-width: 401px) and (max-width: 960px) {
 
    .videodate{
        height: 70vh;
     }
  }


  /* Desktop Styles */
@media only screen and (min-width: 961px) {
.videodate{
   height: 90vh;
}
    

  }
  