* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
  }
  .homcard{
    width: 100% ;
    margin-bottom: 20px;
  }
  .cardshome{
    display: flex;
    justify-content: space-evenly;
    flex-wrap: wrap;
    width: 99%;
    margin: auto;
    margin-bottom: 50px;
  }
/* Tablet Styles */
@media only screen and (min-width: 401px) and (max-width: 960px) {
  .homcard{
    width: 450px ;
    margin-bottom: 20px;
  }
  .cardshome{
    display: flex;
    justify-content: space-evenly;
    flex-wrap: wrap;
    width: 99%;
    margin: auto;
    margin-bottom: 50px;
  }
  }
  /* Desktop Styles */
@media only screen and (min-width: 961px) {

.homcard{
  width: 300px ;
}
.cardshome{
  display: flex;
  justify-content: space-evenly;
  flex-wrap: wrap;
  width: 99%;
  margin: auto;
  margin-bottom: 50px;
}
  }
  