* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
  }
  .aboutkindImage{
    width: 99%;
    height: 70vh;
}
.aboutkindText{

    background-color: black;
    color: white;
    text-align: left;
    padding: 25px;

}



/* Tablet Styles */
@media only screen and (min-width: 401px) and (max-width: 960px) {
 
  }


  /* Desktop Styles */
@media only screen and (min-width: 961px) {
.aboutkindImage{
    width: 650px;
    height: 90vh;
    position: absolute;
    left: 30px;
}
.aboutkindText{
    height: 240px;
    background-color: black;
    color: white;
    text-align: left;
    padding: 25px;
    width: 650px;
    position:relative;
    left: 45%;
    top:30vh;

}

  }
  