* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
  }
  .kindImage{
    width: 99%;
    height: 70vh;
}
.kindText{
  background-color: black;
  color: white;
  margin-top: 10px;
}


/* Tablet Styles */
@media only screen and (min-width: 401px) and (max-width: 960px) {
 
  }


  /* Desktop Styles */
@media only screen and (min-width: 961px) {
.kindImage{
    width: 700px;
    height: 100vh;
    position: absolute;
    left: 30px;
}
.kindText{
    height: 80%;
    background-color: black;
    color: white;
    text-align: left;
    padding: 25px;
    width: 50%;
    position:relative;
    left: 20%;
    top:20vh;
}

  }
  