* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
  }
  .homcarou{
    display: none;
}

/* Tablet Styles */
@media only screen and (min-width: 401px) and (max-width: 960px) {
 .homcarou{
     display: none;
 }
  }
  /* Desktop Styles */
@media only screen and (min-width: 961px) {
  .homcarou{
      display: block;
    width: 95%; 
    margin: auto; 
    margin-top: 80vh;
    margin-bottom: 100px;
  }
  .caroupic{
      height: 100vh;
  }
  }